.MuiContainer-root {
	overflow: auto;
}

/*=============================================*/

* {
	box-sizing: border-box;
}

.app {
	height: 100vh;
	display: grid;
	grid-template: auto 1fr / 1fr;
}

.appbar {
	grid-row-start: 1;
}

.content {
	height: 100%;
	display: grid;
	grid-template-columns: 1fr 4fr 4fr;
	grid-template-rows: repeat(2, auto) repeat(2, 1fr);
	grid-gap: 20px;
	padding: 20px 0;
}

.menu-left-area {
	grid-column: 1 / span 1;
	grid-row: 1 / -1;
}

.menu-left {
	padding: 0 20px 20px;
	max-height: 100%;
	overflow: auto;
}

.stats {
	grid-area: stats;
	display: flex;
	align-items: center;
	grid-column: 2 / span 1;
	grid-row: 1 / span 1;
}


.evolution {
	grid-area: evolution;
	grid-column: 3 / span 1;
	grid-row: 1 / span 1;
	display: flex;
	justify-content: safe center;
	align-items: safe center;
	overflow: auto;
	max-height: 222px;
	padding: 10px;
}

.evolution__content {
	margin: auto;
}

.movelist {
	grid-area: moves;
	overflow: auto;
	height: 100%;
	grid-column: 2 / -1;
	grid-row: 2 / -1;
	/*grid-row: 2 / -1;*/
}

.evo {
	display: flex;
}

.evo__main {
	display: flex;
	justify-content: stretch;
	align-items: center;
	padding: 10px 15px;
	cursor: pointer;
}

.evo__main:hover {
	background-color: lightgray;
}

.evo__condition {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	align-self: center;
	margin: 10px;
}

.evo__requirement {
	font-family: inherit;
	margin: 0;
	font-size: .6rem;
	text-align: center;
}

.evo__evos {
	flex: 1 1 auto;
	display: flex;
	flex-direction: column;
	justify-content: stretch;
	align-items: stretch;
}

.evos__evo {
	display: flex;
	align-items: flex-end;
}